<template>
  <v-card style="float:left;width:282px;margin:15px 0 0 15px;text-align:left" tile class="mx-auto">
    <v-list-item two-line>
      <v-list-item-content>
        <v-list-item-subtitle>干员控制面板</v-list-item-subtitle>
        <v-list-item-title class="headline" style="font-size:28px">艾雅法拉</v-list-item-title>
      </v-list-item-content>
    </v-list-item>
    <div style="height:35px;">
      <v-progress-linear :value="valueDeterminate" :color="color" reactive height="24">
        <strong>{{ Math.round(nowblood)+'/'+Math.round(maxblood) }}</strong>
      </v-progress-linear>
    </div>
    <v-progress-circular :rotate="-90" :size="40" :width="3" :value="value1" color="teal">
      <v-icon>mdi-sword</v-icon>
    </v-progress-circular>
    <v-progress-circular :rotate="-90" :size="40" :width="3" :value="value1" color="teal">
      <v-icon>mdi-shield-half-full</v-icon>
    </v-progress-circular>
    <v-progress-circular :rotate="-90" :size="40" :width="3" :value="value1" color="teal">
      <v-icon>mdi-shield-cross</v-icon>
    </v-progress-circular>
    <v-progress-circular :rotate="-90" :size="40" :width="3" :value="value1" color="teal">
      <v-icon>mdi-clock</v-icon>
    </v-progress-circular>
    <!--<v-slider v-model="valueDeterminate" label="blood"></v-slider>-->
    <div style="width:282px;height:200px;padding:0 16px">
      <value title="攻击力" :value="630" />
      <value title="防御力" :value="100" style="margin-left:10px" />
      <value title="法术抗性" :value="20" />
      <value title="攻击间隔" :value="1.6" style="margin-left:10px" />
    </div>
    <v-card-actions>
      <v-btn text color="deep-purple accent-4">Learn More</v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
/* eslint-disable */
import value from "@/components/part/value.vue";
export default {
  components: {
    value
  },
  data() {
    return {
      maxblood: 3000,
      nowblood: 3000,
      color: "",
      valueDeterminate: 0,
      value1: 60
    };
  },
  created() {
    this.valueDeterminate = (this.nowblood / this.maxblood) * 100;
    this.changeColor();
  },
  watch: {
    valueDeterminate() {
      this.nowblood = (this.maxblood * this.valueDeterminate) / 100;
      this.changeColor();
    }
  },
  methods: {
    xxclick() {
      if (this.nowblood <= this.maxblood) this.nowblood += 50;
    },
    changeColor() {
      let string = "";
      let r;
      let g;
      if (this.valueDeterminate > 50) {
        r = 210 - ((this.valueDeterminate - 50) / 50) * 210;
        g = 210;
      } else {
        r = 210;
        g = (this.valueDeterminate / 50) * 210;
      }
      string = "rgb(" + r + "," + g + ",0)";
      this.color = string;
    }
  }
};
</script>

